import React from 'react';

import clsx from 'clsx';
import { twMerge } from 'tailwind-merge';
import { FunctionalIcon } from '../icon/Icon';
import { PhoneInputProps, PhoneInputScope } from './PhoneInput.interface';
import './react-phone-input-2/lib/style.css';
import ReactPhoneInput from './react-phone-input-2/src/index';

export const PhoneInput: React.FC<PhoneInputProps> = ({
  disabled = false,
  excludeCountries,
  id,
  initialCountry = 'de',
  label,
  message,
  onChange,
  onlyCountries,
  preferredCountries,
  readOnly = false,
  state = 'default',
  value,
  ...props
}) => {
  const hasError = state === 'error';
  const iconColor = hasError ? 'zep-text-error' : state === 'info' ? 'zep-text-info' : '';
  const disabledOrReadonly = disabled || readOnly;
  const inputClasses = clsx('!zep-h-[48px]', '!zep-border-input-placeholder-default', '!zep-text-base', {
    '!zep-bg-background-medium': readOnly,
  });

  const buttonClasses = clsx(
    '!zep-border-[0px]',
    '!zep-bg-transparent',
    '!zep-pl-0.5',
    '!zep-border-button-secondary-color',
    '[&>.arrow]:!zep-text-xl',
    disabled,
    readOnly,
    {
      '!zep-cursor-not-allowed': disabledOrReadonly,
    },
  );

  return (
    <div
      className={twMerge(
        clsx(
          PhoneInputScope,
          'zep-inline-flex',
          'zep-flex-col',
          'zep-flex-wrap',
          'zep-items-stretch',
          'zep-justify-start',
          'zep-relative',
          [disabled ? 'zep-opacity-zep-40 zep-cursor-not-allowed' : 'zep-cursor-pointer'],
          { 'zep-pointer-events-none zep-opacity-disabled': disabled },
        ),
        props.className,
      )}
      data-testid="zep-phone-input"
    >
      {label && (
        <label htmlFor={id} className={clsx('zep-mb-0.5', 'zep-text-input-label-default')}>
          {label}
        </label>
      )}
      <div className={clsx('zep-inline-flex', 'zep-flex-row', 'zep-flex-nowrap', 'zep-items-center', 'zep-gap-0.5')}>
        <div className={clsx('zep-relative', 'zep-w-full')}>
          <ReactPhoneInput
            buttonClass={buttonClasses}
            country={initialCountry}
            disabled={disabledOrReadonly}
            excludeCountries={excludeCountries}
            inputClass={inputClasses}
            onChange={onChange}
            onlyCountries={onlyCountries}
            placeholder={''}
            preferredCountries={preferredCountries}
            value={value}
          />
        </div>
      </div>
      {message && (
        <div className={clsx('zep-inline-flex', 'zep-flex-row', 'zep-items-center', 'foo', 'zep-mt-0.25', iconColor)}>
          {state !== 'default' && (
            <FunctionalIcon
              name={state === 'error' ? 'warning-triangle-filled' : 'info-circle-filled'}
              role="img"
              size={24}
              className="zep-mr-0.5"
            />
          )}
          <span id={`aria-describedby-${id}`}>{message}</span>
        </div>
      )}
    </div>
  );
};
